import React, { useContext } from "react";
import { ValorMonetarioContext } from "../../contextos/ValorMonetario";
import DetalleTransaccion from "../TransaccionesComponentes/DetalleTransaccion";
import '../../estilos/DetallesEstilos/UltimasTransacciones.css'

function UltimasTransacciones() {

  const {transacciones} = useContext(ValorMonetarioContext)

  const ultimasTransacciones = transacciones.slice(-3).reverse();

  return (
    <article className="pagina__detalles-componente-ultimas-transacciones">
      <h3>Ultimas transacciones</h3>
      <div className="pagina__detalles-componente-ultimas-transacciones-contenedor">
      {ultimasTransacciones.map(item => (
          <DetalleTransaccion
            key={item.id}
            id= {item.id}
            tipo = {item.tipo}
            valor = {item.valor}
            fecha = {item.fecha}
            categoria = {item.categoria}
          />
        ))}
      </div>
    </article>
  )
}

export default UltimasTransacciones