import React, { useContext } from "react";
import '../estilos/TransaccionesEstilos/PaginaTransacciones.css'
import DetalleTransaccion from "../componentes/TransaccionesComponentes/DetalleTransaccion";
import { ValorMonetarioContext } from "../contextos/ValorMonetario";

function PaginaTransacciones() {

  const {transacciones} = useContext(ValorMonetarioContext)

  return (
    <section className="pagina__transacciones">
      <h1 className="pagina__transacciones-titulo">Transacciones Recientes</h1>
      <div className="pagina__transacciones-contenedor">
        {transacciones.map(item => (
          <DetalleTransaccion
            key={item.id}
            id= {item.id}
            tipo = {item.tipo}
            valor = {item.valor}
            fecha = {item.fecha}
            categoria = {item.categoria}
          />
        ))}
      </div>
    </section>
  ) 
}

export default PaginaTransacciones;
