import React from "react";
import '../../estilos/TransaccionesEstilos/DetalleTransaccion.css'

function DetalleTransaccion({tipo , valor , fecha, categoria}) {
  return (
    <article className="pagina__transacciones-componente">
      <div className="pagina__transacciones-componente-contenedor-izquierda">
        <p>{categoria}</p>
        <span>{fecha}</span>
      </div>
      <div className="pagina__transacciones-componente-contenedor-derecha">
        <p className={tipo === 'ingreso' ? 'transaccion-positiva' : 'transaccion-negativa'}>
          {tipo === 'ingreso' ? '+' : '-'}${valor}
        </p>  
      </div>   
     </article>
  )
}

export default DetalleTransaccion