import React from "react";
import '../../estilos/PrincipalEstilos/Encabezado.css'
import { Link } from "react-router-dom";

function Encabezado() {
  return (
    <header className="encabezado">
      <div className="encabezado__contenedor-left">
        <span className="encabezado-icono"></span>
        <h1 className="encabezado-logo">SaTriXFinanzas</h1>
      </div>
      <nav className="encabezado__nav">
        <Link to="/" className="encabezado__nav-items"><a href="#" className="encabezado__nav-link">Inicio</a></Link>
        <Link to="/transacciones" className="encabezado__nav-items"><a href="#" className="encabezado__nav-link">Transacciones</a></Link>
        <Link to='/detalles' className="encabezado__nav-items"><a href="#" className="encabezado__nav-link">Detalles</a></Link>
      </nav>
    </header>
  )
}

export default Encabezado