import React, { useContext } from "react";
import { Doughnut } from "react-chartjs-2";
import '../../estilos/DetallesEstilos/DesgloseDeGastosCategorias.css';
import { ValorMonetarioContext } from "../../contextos/ValorMonetario";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';

ChartJS.register(ArcElement, Tooltip, Legend, ChartDataLabels);

function DesgloseDeGastosCategorias() {
  const { transacciones } = useContext(ValorMonetarioContext);

  // Filtrar y agrupar los gastos por categora
  const gastosPorCategoria = transacciones
    .filter(transaccion => transaccion.tipo === 'gasto')
    .reduce((acc, transaccion) => {
      const { categoria, valor } = transaccion;
      if (!acc[categoria]) acc[categoria] = 0;
      acc[categoria] += parseFloat(valor);
      return acc;
    }, {});

  // Crear etiquetas y valores para el grfico
  const labels = Object.keys(gastosPorCategoria);
  const dataValues = Object.values(gastosPorCategoria);

  const data = {
    labels,
    datasets: [
      {
        data: dataValues,
        backgroundColor: [
          '#FF6384', '#36A2EB', '#FFCE56', '#4BC0C0', '#9966FF', '#FF9F40'
        ],
      },
    ],
  };

  const options = {
    plugins: {
      datalabels: {
        formatter: (value) => {
          const total = dataValues.reduce((acc, curr) => acc + curr, 0);
          const percentage = ((value / total) * 100).toFixed(2);
          return `${percentage}%`;
        },
        color: '#fff',
        font: {
          size: 14,
        },
      },
      legend: {
        display: true,
        position: 'bottom',
      },
    },
  };

  return (
    <article className="pagina__detalles-componente-desglose-gastos-categorias">
      <h3>Desglose de Gastos por Categoras</h3>
      <div className="pagina__detalles-componente-grafico-contenedor">
        <Doughnut data={data} options={options} />
      </div>
    </article>
  );
}

export default DesgloseDeGastosCategorias;
