import React, { useContext } from "react";
import { Doughnut } from "react-chartjs-2";
import '../../estilos/DetallesEstilos/GraficoIngresosVsGastos.css';
import { ValorMonetarioContext } from "../../contextos/ValorMonetario";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';

ChartJS.register(ArcElement, Tooltip, Legend, ChartDataLabels);

function GraficoIngresosVsGastos() {
  const { transacciones } = useContext(ValorMonetarioContext);

  // filtramos por 
  const totalIngresos = transacciones
    .filter(transaccion => transaccion.tipo === 'ingreso')
    .reduce((total, transaccion) => total + parseFloat(transaccion.valor), 0);

  const totalGastos = transacciones
    .filter(transaccion => transaccion.tipo === 'gasto')
    .reduce((total, transaccion) => total + parseFloat(transaccion.valor), 0);

  const total = totalIngresos + totalGastos;

  // plugging 
  const data = {
    labels: ['Ingresos', 'Gastos'],
    datasets: [
      {
        data: [totalIngresos,totalGastos],
        backgroundColor: ['#36A2EB', '#FF6384'],
      },
    ],
  };

  const options = {
    plugins: {
      datalabels: {
        formatter: (value) => {
          const percentage = ((value / total) * 100).toFixed(2);
          return `${percentage}%`;
        },
        color: '#fff',
        font: {
          size: 14,
        },
      },
      legend: {
        display: true,
        position: 'bottom',
      },
    },
  };

  return (
    <article className="pagina__detalles-componente-grafico-ingresos-gastos">
      <h3>Grafico de Ingresos vs Gastos</h3>
      <div className="pagina__detalles-componente-grafico-contenedor">
        <Doughnut data={data} options={options} />
      </div>
    </article>
  );
}

export default GraficoIngresosVsGastos;
