import React, { useContext } from "react";
import '../estilos/PrincipalEstilos/Principal.css'
import Resumen from "../componentes/PrincipalComponentes/Resumen";
import Gastos from "../componentes/PrincipalComponentes/Gastos";
import Acciones from "../componentes/PrincipalComponentes/Acciones";
import { ManejarClickContext } from "../contextos/ManejarClick";
import Form from "../componentes/PrincipalComponentes/Form";

function Principal() {

  const {clickAgregarIngreso , clickAgregarGasto} = useContext(ManejarClickContext)

  return (
    <main className="principal">
      <Resumen/>
      <Gastos/>
      <Acciones/>

      {clickAgregarGasto || clickAgregarIngreso ?
        (
          <Form/>
        ):
        (
          ''
        )
      } 
    </main>
  )
}


export default Principal