import React from "react";
import '../estilos/DetallesEstilos/PaginaDetalles.css'
import ResumenGeneral from "../componentes/DetallesComponentes/ResumenGeneral";
import GraficoIngresosVsGastos from "../componentes/DetallesComponentes/GraficoIngresosVsGastos";
import DesgloseDeGastosCategorias from "../componentes/DetallesComponentes/DesgloseDeGastosCategorias";
import UltimasTransacciones from "../componentes/DetallesComponentes/UltimasTransacciones";

function PaginaDetalles() {
  return (
    <section className="pagina__detalles">
      <h1>Detalles del Resumen del Mes</h1>
      <ResumenGeneral/>
      <GraficoIngresosVsGastos/>
      <DesgloseDeGastosCategorias/>
      <UltimasTransacciones/>
    </section>
  )
}

export default PaginaDetalles