import React, { useContext, useEffect, useState } from "react";
import '../../estilos/DetallesEstilos/ResumenGeneral.css'
import { ValorMonetarioContext } from "../../contextos/ValorMonetario";


function ResumenGeneral() {

	const {transacciones} = useContext(ValorMonetarioContext)

	const [totalIngresos ,setTotalIngresos] = useState(0)
	const [totalGastos ,setTotalGastos] = useState(0)
	const [totalBalance ,setTotalBalance] = useState(0)
	const [totalAhorro ,setTotalAhorro] = useState(0)

	useEffect(() => {
		const ingresos = transacciones
		.filter(transaccion => transaccion.tipo === 'ingreso')
		.reduce((total, transaccion) => total + parseFloat(transaccion.valor),0)

		const gastos = transacciones
		.filter(transaccion => transaccion.tipo === 'gasto')
		.reduce((total, transaccion) => total + parseFloat(transaccion.valor),0)

		setTotalIngresos(ingresos);
		setTotalGastos(gastos);
		setTotalBalance(ingresos - gastos);
		setTotalAhorro(((ingresos - gastos) / ingresos) * 100)
	},[transacciones])

	return (
		<article className="pagina__detalles-componente-resumen">
			<h3>Resumen General</h3>
			<div className="pagina__detalles-componente-resumen-contenedor-grilla">
				<div class="pagina__detalles-componente-resumen-contenedor-items">
					<span>Ingresos Totales</span>
					<p className='pagina__detalles-componente-resumen-contenedor-item positivo'>${totalIngresos.toFixed(2)}</p>
				</div>
				<div class="pagina__detalles-componente-resumen-contenedor-items">
					<span>Gastos Totales</span>
					<p className="pagina__detalles-componente-resumen-contenedor-item negativo">${totalGastos.toFixed(2)}</p>
				</div>
				<div class="pagina__detalles-componente-resumen-contenedor-items">
					<span>Balance</span>
					<p className="pagina__detalles-componente-resumen-contenedor-item">${totalBalance.toFixed(2)}</p>
				</div>
				<div class="pagina__detalles-componente-resumen-contenedor-items">
					<span>Ahorro</span>
					<p className="pagina__detalles-componente-resumen-contenedor-item">{totalAhorro.toFixed(2)}%</p>
				</div>
			</div>
		</article>
	)
}

export default ResumenGeneral